<template>
    <nav class="navbar orange lighten-1">
        <div class="nav-wrapper">
            <div class="navbar-left">
                <a href="#" @click.prevent="$emit('hidePanel')">
                    <i class="material-icons black-text">dehaze</i>
                </a>
                <span class="black-text">{{ new Intl.DateTimeFormat('ru-RU', {
                    weekday: 'short',
                    //era: 'long',
                    year: 'numeric',
                    month: 'short',
                    day: '2-digit',
                    hour: '2-digit',
                    minute: 'numeric',
                    second: '2-digit'
                }).format() }}</span>
            </div>

            <ul class="right hide-on-small-and-down">
                <li>
                    <a
                        class="dropdown-trigger black-text"
                        href="#" 
                        data-target="dropdown"
                        ref="logindropdown"
                    >
                        USER NAME
                        <i class="material-icons right">arrow_drop_down</i>
                    </a>

                    <ul id='dropdown' class='dropdown-content'>
                        <li>
                            <router-link to="/profile" class="black-text">
                                <i class="material-icons">account_circle</i>Профиль
                            </router-link>
                        </li>
                        <li class="divider" tabindex="-1"></li>
                        <li>
                            <a href="#" class="black-text" @click.prevent="logout">
                                <i class="material-icons">assignment_return</i>Выйти
                            </a>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
    </nav>
</template>

<script>
import M from 'materialize-css/dist/js/materialize.min'

    export default {
        data: () => ({
            date: new Date(),
            interval: null,
            mydropdown: null,
        }),
        
        methods: {
            logout() {
                console.log('logo')
                this.$router.push('/login?user-message=logout') //редирект с дополнительным сообщением в адресной строке
            }
        },

        mounted() {
            //для обработки времени на панели навигации
            this.interval = setInterval(() => {
                this.date = new Date()
            }, 1000)

            //console.log(this.$refs.logindropdown)
            this.mydropdown =  M.Dropdown.init(this.$refs.logindropdown, {
                constrainWidth: false //поставил свойство, пока не очень понятно как работает
            })
        },

        beforeUnmount() {
            console.log('destr')
            clearInterval(this.interval)
            if(this.mydropdown && this.mydropdown.destroy) {
                this.mydropdown.destroy()
            }
        }
    }
</script>